* {
    margin: 0;
    padding: 0;
  }
  
  body {
    /*background-color: #fffdee;*/
    background-image: url(assets/시작화면.jpg);
    background-size: cover;
    background-attachment: fixed;
    flex-direction: column;
    padding-top: 20vh;
    padding-left: 10vw;
    display: flex;
    position: fixed;
    width: 90vw;
    height: 80vh;
    overflow: hidden;
  }
  
  .btn {
    width: max-content;
    height: max-content;
    min-width: 150px;
    text-align: center;
    color: #3b2409;
    background-color: #01d281;
    border: 3.5px solid #6f573c;
    border-radius: 15px;
    margin-top: 5vh;
    padding: 8px 10px;
    font-size: 3vh;
    font-weight: 600;
  }
  
  h1 {
    font-size: 5vh;
    font-weight: 600;
  }
  
  h3 {
    margin-bottom: 4px;
    font-size: 3vh;
    font-weight: 600;
  }
  
  .title {
    color: #3b2409;
  }
  
  .ku_img {
    width: 100vw;
    max-width: 450px;
    position: absolute;
    bottom: -50px;
    right:-60px;
  }